import React, { useEffect, useMemo, useRef, useState } from "react";
import Typewriter from "typewriter-effect/dist/core";
import tringleicon from "../../assets/images/traingle-icon.png";
import dotspattern from "../../assets/images/dots-pattern.png";
import hoverAudio from "../../assets/audios/hover.m4a";
import clickAudio from "../../assets/audios/click.mp3";

function CTA({ setActiveLink, activeLink }) {
  const viewportRef = useRef(null);
  const isInViewport = useIsInViewport(viewportRef);
  const handleMouseEnter = () => new Audio(hoverAudio).play();
  const handleMouseClick = () => new Audio(clickAudio).play();

  useEffect(() => {
    if (isInViewport) {
      setActiveLink({
        ...activeLink,
        banner: false,
        intro: false,
        lore: false,
        features: false,
        gravitars: false,
        roadmap: false,
        cta: true,
      });
    } else {
      // setActiveLink({ ...activeLink, banner: false, intro: false, lore: false, features: false, gravitars: false, roadmap: false , cta: false });
    }
  }, [isInViewport]);

  function useIsInViewport(ref) {
    const [isIntersecting, setIsIntersecting] = useState(false);

    const observer = useMemo(
      () =>
        new IntersectionObserver(([entry]) =>
          setIsIntersecting(entry.isIntersecting)
        ),
      []
    );

    useEffect(() => {
      observer.observe(ref.current);

      return () => {
        observer.disconnect();
      };
    }, [ref, observer]);

    return isIntersecting;
  }

  const typewriterDiv = document.getElementById("typewriterCTA");
  const typewriter = new Typewriter(typewriterDiv, {
    loop: true,
    delay: 75,
    autoStart: true,
    deleteSpeed: 100,
    // cursor: "",
  });

  typewriter
    .typeString(
      "Explore the <span>Infinite expanses </span> of the galaxy"
    )
    .pauseFor(1000)
    .start();

  return (
    <div className="feature-sec feature-even-sec cta-sec">
      <div ref={viewportRef} className="container">
        <div className="feature-heading-sec">
          <div id="typewriterCTA" className="cta-heading-sec"></div>
        </div>
        <a
          href="https://www.google.com/"
          target="_blank"
          className="common-btn"
          onMouseEnter={handleMouseEnter}
          onClick={handleMouseClick}
        >
          JOIN GRAVITALE
        </a>
        <h1 className="placeholder-text">CTA</h1>
      </div>

      <img src={dotspattern} className="dots-pattern" />
    </div>
  );
}

export default CTA;
