import React, { useEffect, useMemo, useRef, useState } from "react";
import dotspattern from "../../assets/images/dots-pattern.png";
import Typewriter from "typewriter-effect/dist/core";

function Introduction({ setActiveLink, activeLink }) {
  const viewportRef = useRef(null);
  const isInViewport = useIsInViewport(viewportRef);

  useEffect(() => {
    if (isInViewport) {
      setActiveLink({
        ...activeLink,
        banner: false,
        intro: true,
        lore: false,
        features: false,
        gravitars: false,
        roadmap: false,
        cta: false,
      });
    }
  }, [isInViewport]);
  var TxtType = function(el, toRotate, period) {
    this.toRotate = toRotate;
    this.el = el;
    this.loopNum = 0;
    this.period = parseInt(period, 10) || 2000;
    this.txt = '';
    this.tick();
    this.isDeleting = false;
};

TxtType.prototype.tick = function() {
    var i = this.loopNum % this.toRotate.length;
    var fullTxt = this.toRotate[i];

    if (this.isDeleting) {
    this.txt = fullTxt.substring(0, this.txt.length - 1);
    } else {
    this.txt = fullTxt.substring(0, this.txt.length + 1);
    }

    this.el.innerHTML = '<span class="wrap">'+this.txt+'</span>';

    var that = this;
    var delta = 200 - Math.random() * 100;

    if (this.isDeleting) { delta /= 2; }

    if (!this.isDeleting && this.txt === fullTxt) {
    delta = this.period;
    this.isDeleting = true;
    } else if (this.isDeleting && this.txt === '') {
    this.isDeleting = false;
    this.loopNum++;
    delta = 500;
    }

    setTimeout(function() {
    that.tick();
    }, delta);
};

window.onload = function() {
    var elements = document.getElementsByClassName('typewrite');
    for (var i=0; i<elements.length; i++) {
        var toRotate = elements[i].getAttribute('data-type');
        var period = elements[i].getAttribute('data-period');
        if (toRotate) {
          new TxtType(elements[i], JSON.parse(toRotate), period);
        }
    }
    // INJECT CSS
    var css = document.createElement("style");
    css.type = "text/css";
    css.innerHTML = ".typewrite > .wrap { border-right: 0.08em solid #fff}";
    document.body.appendChild(css);
};
  function useIsInViewport(ref) {
    const [isIntersecting, setIsIntersecting] = useState(false);

    const observer = useMemo(
      () =>
        new IntersectionObserver(([entry]) =>
          setIsIntersecting(entry.isIntersecting)
        ),
      []
    );

    useEffect(() => {
      observer.observe(ref.current);

      return () => {
        observer.disconnect();
      };
    }, [ref, observer]);

    return isIntersecting;
  }

  const typewriterDiv = document.getElementById("typewriterIntro");
  const typewriter = new Typewriter(typewriterDiv, {
    loop: true,
    delay: 75,
    autoStart: true,
    deleteSpeed: 100,
    // cursor: "",
  });

  typewriter
    .typeString(
      "Gravitale is a virtual world with <span>stunning graphics</span>, <br /> and <span>customization</span>.<br /> Explore a vast world and compete in various <br /> activities, <span>all in one platform</span>."
    )
    .pauseFor(1000)
    .start();

  return (
    <div id="intro" className="introduction-sec">
      <div className="container">
        <div className="introduction-desc">
          <div id="typewriterIntro" className="intro-heading-text" ref={viewportRef}></div>
          <h1 className="placeholder-text">INTRO</h1>
        </div>
      </div>
      <img src={dotspattern} className="dots-pattern" alt="" />
    </div>
  );
}

export default Introduction;
