import * as THREE from "three";
import React, { useEffect, useRef, useState } from "react";
import { useGLTF, useAnimations, PerspectiveCamera } from "@react-three/drei";
import { useFrame } from "@react-three/fiber";
const color = new THREE.Color();

export function Gravitalev4(scroll, props) {
  const group = useRef();
  const { nodes, materials, animations } = useGLTF("./models/gravitalev4.glb");
  const { actions } = useAnimations(animations, group);
  const [hovered, setHover] = useState();
  const extras = {
    receiveShadow: true,
    castShadow: true,
    "material-envMapIntensity": 0.2,
  };
  useEffect(() => void (actions["Animation"].play().paused = true), []);
  useEffect(() => {
    if (hovered) {
      group.current.getObjectByName(hovered).material.color.set("white");
      document.body.style.cursor = "pointer";
    } else {
      document.body.style.cursor = "auto";
    }
  }, [hovered]);
  useFrame((state) => {
    actions["Animation"].time = THREE.MathUtils.lerp(
      actions["Animation"].time,
      actions["Animation"].getClip().duration * scroll.scroll.current,
      0.05
    );
    group.current.children[0].children.forEach((child, index) => {
      child.material.color.lerp(
        color.set(hovered === child.name ? "tomato" : "#ffffff"),
        hovered ? 0.01 : 0.05
      );
      const et = state.clock.elapsedTime;
      // child.position.y = Math.sin((et + index * 2000) / 2) * 1;
      // child.rotation.x = Math.sin((et + index * 2000) / 3) / 10;
      // child.rotation.y = Math.cos((et + index * 2000) / 2) / 10;
      // child.rotation.z = Math.sin((et + index * 2000) / 3) / 10;
    });
  });

  return (
    <group ref={group} {...props} dispose={null}>
      <group
        onPointerOver={(e) => (e.stopPropagation(), setHover(e.object.name))}
        onPointerOut={(e) => (e.stopPropagation(), setHover(null))}
        name="Scene"
      >
        <mesh
          name="9"
          geometry={nodes["9"].geometry}
          material={materials.lambert1}
          rotation={[Math.PI / 2, 0, -0.09]}
          scale={0.01}
          {...extras}
        />
        <mesh
          name="1"
          geometry={nodes["1"].geometry}
          material={materials.lambert1}
          rotation={[Math.PI / 2, 0, 0]}
          scale={0.01}
          {...extras}
        />
        <mesh
          name="2"
          geometry={nodes["2"].geometry}
          material={materials.lambert1}
          rotation={[Math.PI / 2, 0, 0.09]}
          scale={0.01}
          {...extras}
        />
        <mesh
          name="3"
          geometry={nodes["3"].geometry}
          material={materials.lambert1}
          rotation={[Math.PI / 2, 0, 0.19]}
          scale={0.01}
          {...extras}
        />
        <mesh
          name="4"
          geometry={nodes["4"].geometry}
          material={materials.lambert1}
          rotation={[Math.PI / 2, 0, 0.29]}
          scale={0.01}
          {...extras}
        />
        <mesh
          name="5"
          geometry={nodes["5"].geometry}
          material={materials.lambert1}
          rotation={[Math.PI / 2, 0, 0.37]}
          scale={0.01}
          {...extras}
        />
        <mesh
          name="6"
          geometry={nodes["6"].geometry}
          material={materials.lambert1}
          rotation={[Math.PI / 2, 0, 0.47]}
          scale={0.01}
          {...extras}
        />
        <mesh
          name="8"
          geometry={nodes["8"].geometry}
          material={materials.lambert1}
          rotation={[Math.PI / 2, 0, 0]}
          scale={0.01}
          {...extras}
        />
        <mesh
          name="7"
          geometry={nodes["7"].geometry}
          material={materials.lambert1}
          rotation={[Math.PI, -0.32, Math.PI]}
          scale={0.02}
          {...extras}
        />
      </group>
      <group
        name="Camera"
        position={[-15.33, 3.29, 1.56]}
        rotation={[1.59, -0.04, 1.85]}
      >
        <PerspectiveCamera
          name="Camera_Orientation"
          makeDefault={true}
          far={100}
          near={0.1}
          fov={22.9}
          rotation={[-Math.PI / 2, 0, 0]}
        >
          <directionalLight
            castShadow
            position={[10, 20, 15]}
            shadow-camera-right={8}
            shadow-camera-top={8}
            shadow-camera-left={-8}
            shadow-camera-bottom={-8}
            shadow-mapSize-width={1024}
            shadow-mapSize-height={1024}
            intensity={1.5}
            shadow-bias={-0.0001}
          />
        </PerspectiveCamera>
      </group>
    </group>
  );
}

useGLTF.preload("./models/gravitalev4.glb");
