import React from "react";
import footerlogo from "../../assets/images/footer-logo.png";
import hoverAudio from "../../assets/audios/hover.m4a";
import clickAudio from "../../assets/audios/click.mp3";

function Footer() {
  const handleMouseEnter = () => new Audio(hoverAudio).play();
  const handleMouseClick = () => new Audio(clickAudio).play();
  return (
    <div>
      <div className="footer-sec">
        <div className="container">
          <div className="footer-inner-sec">
            <img src={footerlogo} />
            <ul>
              <li>
                <a   onMouseEnter={handleMouseEnter}
                onClick={handleMouseClick}>
                  <i class="fa fa-twitter" aria-hidden="true"></i>
                </a>
              </li>
              <li>
                <a   onMouseEnter={handleMouseEnter}
                onClick={handleMouseClick}>
                  <i class="fa fa-instagram" aria-hidden="true"></i>
                </a>
              </li>
              <li>
                <a   onMouseEnter={handleMouseEnter}
                onClick={handleMouseClick}>
                  <i class="fa fa-facebook" aria-hidden="true"></i>
                </a>
              </li>
            </ul>
          </div>
        </div>
      </div>
      <div className="copyright-sec">
          <div className="container">
            <p>copyright &copy; 2023 gravitale all rights reserved</p>
          </div>
      </div>
    </div>
  );
}

export default Footer;
