import React, { useEffect, useRef, useState  } from "react";
import {
  Collapse,
  Navbar,
  NavbarToggler,
  NavbarBrand,
  Nav,
  NavItem,
  NavLink,
  UncontrolledDropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem,
  NavbarText,
} from "reactstrap";
import logo from "../../assets/images/logo.png";
import hoverAudio from "../../assets/audios/hover.m4a";
import clickAudio from "../../assets/audios/click.mp3";
import AnchorLink from "react-anchor-link-smooth-scroll";

function Header({ activeLink }) {
  const [isOpen, setIsOpen] = useState(false);
  const toggle = () => setIsOpen(!isOpen);
  const [scroll, setScroll] = useState(false);
  useEffect(() => {
    window.addEventListener("scroll", () => {
      setScroll(window.scrollY > 50);
    });
  }, []);

  const handleMouseEnter = () => new Audio(hoverAudio).play();
  const handleMouseClick = () => new Audio(clickAudio).play();

  return (
    <div>
      <Navbar expand="lg" className={scroll ? "header sticky" : "header"}>
        <NavbarBrand>
          <img src={logo} alt="" />
        </NavbarBrand>
        <NavbarToggler
          onClick={toggle}
          className={isOpen === true ? "toggle-btn-open" : ""}
        >
          <span></span>
          <span></span>
          <span></span>
        </NavbarToggler>
        <div onClick={toggle} className={isOpen ? "toggler-div" : "d-none"}></div>
        <div className="container">
          <Collapse isOpen={isOpen} navbar>
            <Nav className="me-auto" navbar>
              <NavItem className={activeLink.banner ? "active" : ""}>
                <NavLink href="/components/">
                  <AnchorLink onClick={toggle} href="#banner">Hero</AnchorLink>
                </NavLink>
              </NavItem>
              <NavItem className={activeLink.intro ? "active" : ""}>
                <NavLink>
                  <AnchorLink onClick={toggle} href="#intro">INTRODUCTION</AnchorLink>
                </NavLink>
              </NavItem>
              <NavItem className={activeLink.lore ? "active" : ""}>
                <NavLink>
                  <AnchorLink onClick={toggle} href="#lore">LORE</AnchorLink>
                </NavLink>
              </NavItem>
              <NavItem className={activeLink.features ? "active" : ""}>
                <NavLink>
                  <AnchorLink onClick={toggle} href="#features">FEATURES</AnchorLink>
                </NavLink>
              </NavItem>
              <NavItem className={activeLink.gravitars ? "active" : ""}>
                <NavLink>
                  <AnchorLink onClick={toggle} href="#gravitars">GRAVITARS</AnchorLink>
                </NavLink>
              </NavItem>
              <NavItem
                className={activeLink.roadmap || activeLink.cta ? "active" : ""}
              >
                <NavLink>
                  <AnchorLink onClick={toggle} href="#roadmap">ROADMAP</AnchorLink>
                </NavLink>
              </NavItem>
            </Nav>
          </Collapse>
        </div>
        <NavLink
          onMouseEnter={handleMouseEnter}
          onClick={handleMouseClick}
          href="https://www.google.com/"
          target="_blank"
          className="common-btn"
        >
          Join Us
        </NavLink>
      </Navbar>
    </div>
  );
}

export default Header;
