import React, { useEffect, useMemo, useRef, useState } from "react";
import ringimg from "../../assets/images/ring-img.png";
import roadmapimg1 from "../../assets/images/roadmapimg1.png";
import dotspattern from "../../assets/images/dots-pattern.png";
import tringleicon from "../../assets/images/traingle-icon.png";

function Roadmap({ setActiveLink, activeLink }) {
  const viewportRef = useRef(null);
  const isInViewport = useIsInViewport(viewportRef);

  useEffect(() => {
    if (isInViewport) {
      setActiveLink({
        ...activeLink,
        banner: false,
        intro: false,
        lore: false,
        features: false,
        gravitars: false,
        roadmap: true,
        cta: false,
      });
    } else {
      // setActiveLink({ ...activeLink, banner: false, intro: false, lore: false, features: false, gravitars: false, roadmap: false , cta: false });
    }
  }, [isInViewport]);

  function useIsInViewport(ref) {
    const [isIntersecting, setIsIntersecting] = useState(false);

    const observer = useMemo(
      () =>
        new IntersectionObserver(([entry]) =>
          setIsIntersecting(entry.isIntersecting)
        ),
      []
    );

    useEffect(() => {
      observer.observe(ref.current);

      return () => {
        observer.disconnect();
      };
    }, [ref, observer]);

    return isIntersecting;
  }

  const [dataRef, setData] = useState(0);
  const [isDataView, setIsDataView] = useState(false);

  const roadMapData = [
    {
      data: "Lorem ipsum dolor sit amet consectetur adipisicing elit. Dolores amet nisi eligendi accusantium nam sed, optio voluptate modi, autem reprehenderit, ullam totam. Quod deleniti quaerat quos possimus expedita voluptatibus error at tempore perferendis tenetur. Porro dolorem at inventore dolores alias!",
      img: roadmapimg1,
    },
    {
      data: "Lorem ipsum dolor sit amet consectetur adipisicing elit. Dolores amet nisi eligendi accusantium nam sed, optio voluptate modi, autem reprehenderit, ullam totam. Quod deleniti quaerat quos possimus expedita voluptatibus error at tempore perferendis tenetur. Porro dolorem at inventore dolores alias!",
      img: roadmapimg1,
    },
    {
      data: "Lorem ipsum dolor sit amet consectetur adipisicing elit. Dolores amet nisi eligendi accusantium nam sed, optio voluptate modi, autem reprehenderit, ullam totam. Quod deleniti quaerat quos possimus expedita voluptatibus error at tempore perferendis tenetur. Porro dolorem at inventore dolores alias!",
      img: roadmapimg1,
    },
    {
      data: "Lorem ipsum dolor sit amet consectetur adipisicing elit. Dolores amet nisi eligendi accusantium nam sed, optio voluptate modi, autem reprehenderit, ullam totam. Quod deleniti quaerat quos possimus expedita voluptatibus error at tempore perferendis tenetur. Porro dolorem at inventore dolores alias!",
      img: roadmapimg1,
    },
  ];

  const clickHandler = (index) => setData(index);
  const handleMessageView = () => {
    isDataView ? setIsDataView(false) : setIsDataView(true);
  };

  return (
    <div id="roadmap" className="roadmap-sec">
      <div className="container">
        <div className="roadmap-heading-sec">
          <h2>
            Gravitale <span>Space</span> Exploration
          </h2>
        </div>
        <div className="roadmap-sec">
          <img src={ringimg} alt="" className="ring-img" />
          <ul>
            <li onClick={() => clickHandler(0)} className="first">
              <a href="javascript:void(0)">
                {" "}
                Partner themed <span>Islands</span>
              </a>
            </li>
            <li onClick={() => clickHandler(1)} className="second">
              <a href="javascript:void(0)">
                {" "}
                Combine <span>Lands</span>
              </a>
            </li>
            <li onClick={() => clickHandler(2)} className="third">
              <a href="javascript:void(0)">
                {" "}
                Further <span>Gamification</span>
              </a>
            </li>
            <li onClick={() => clickHandler(3)} className="fourth">
              <a href="javascript:void(0)">
                {" "}
                Partner themed <span>Islands</span>
              </a>
            </li>
          </ul>
          <div className="roadmap-data">
            <div className="roadmap-heading">
              <h3>Partner theme <span>Island</span></h3>
            </div>
            <div
              onClick={handleMessageView}
              ref={viewportRef}
              className="roadmap-img"
            >
              <img src={roadMapData[dataRef].img} alt="roadmapimg" />
            </div>
            <div className={isDataView ? "roadmap-desc" : "d-none"}>
              <p>{roadMapData[dataRef].data}</p>
            </div>
          </div>
        </div>
      </div>
      <img src={dotspattern} className="dots-pattern" />
      <img src={tringleicon} className="tringleicon" />
    </div>
  );
}

export default Roadmap;
