import React, { useState, useEffect, useRef, useMemo } from "react";
import "../../assets/scss/Home.scss";
import "../../assets/css/font-awesome.css";
import bannersqare from "../../assets/images/banner-sqaure.png";
import tringleicon from "../../assets/images/traingle-icon.png";
import dotspattern from "../../assets/images/dots-pattern.png";
import bgaudio from "../../assets/audios/gravity-elevator-109356.mp3";
import clichereimg from "../../assets/images/click-here-sound-img.png";
import hoverAudio from "../../assets/audios/hover.m4a";
import clickAudio from "../../assets/audios/click.mp3";

function Banner({ setActiveLink, activeLink }) {
  const bgaudioRef = useRef(null);
  const [isAudioPlaying, setIsAudioPlaying] = useState(false);
  const viewportRef = useRef(null);
  const isInViewport = useIsInViewport(viewportRef);
  const [exploreDiv, setExploreDiv] = useState(true);
  const handleMouseEnter = () => new Audio(hoverAudio).play();
  const handleMouseClick = () => new Audio(clickAudio).play();
  const hideDiv = () => {
    setExploreDiv(false);
    handlePlayPause();
    document.getElementById("body").classList.remove("active");
  };
  const handlePlayPause = () => {
    handleMouseClick();
    if (isAudioPlaying) {
      bgaudioRef.current.pause();
    } else {
      bgaudioRef.current.play();
    }
    setIsAudioPlaying(!isAudioPlaying);
  };

  useEffect(() => {
    if (isInViewport) {
      setActiveLink({
        ...activeLink,
        banner: true,
        intro: false,
        lore: false,
        features: false,
        gravitars: false,
        roadmap: false,
        cta: false,
      });
    } else {
      // setActiveLink({
      //   ...activeLink,
      //   intro: false,
      //   lore: false,
      //   features: false,
      //   gravitars: false,
      //   roadmap: false,
      //   cta: false,
      // });
    }
  }, [isInViewport]);

  function useIsInViewport(ref) {
    const [isIntersecting, setIsIntersecting] = useState(false);

    const observer = useMemo(
      () =>
        new IntersectionObserver(([entry]) =>
          setIsIntersecting(entry.isIntersecting)
        ),
      []
    );

    useEffect(() => {
      observer.observe(ref.current);

      return () => {
        observer.disconnect();
      };
    }, [ref, observer]);

    return isIntersecting;
  }
  return (
    <div>
      <div
        onClick={hideDiv}
        style={{ display: exploreDiv ? "flex" : "none" }}
        className="click-enter-sec"
      >
        <img src={clichereimg} alt="clickhereimg" />
      </div>
      <div id="banner" className="home-banner">
        <div className="container">
          <div className="sub-container">
            <img src={bannersqare} className="bannersqare" />
            <div className="banner-heading-sec">
              <h1>
                GRAVI<span>TALE</span>
              </h1>
            </div>
            <div className="banner-desc-sec">
              <div>
                <h3 ref={viewportRef} className="normal-text">
                  Build, Play <br />
                  and
                  <br />
                  become your
                  <br />
                  <span>Digital self</span>.
                </h3>
                <h3 className="border-text">
                  Build, Play <br />
                  and
                  <br />
                  become your
                  <br />
                  <span>Digital self</span>.
                </h3>
              </div>
            </div>
            <div className="banner-footer-desc">
              <p>
                <span>10,000</span> FLOATING ISLAND LAUNCHING SOON...
              </p>
              <a
                onMouseEnter={handleMouseEnter}
                onClick={handleMouseClick}
                href="https://www.google.com/"
                target="_blank"
                className="common-btn"
              >
                EXPLORE GRAVITALE
              </a>
            </div>
            <div className="banner-footer">
              <audio ref={bgaudioRef} src={bgaudio} autoPlay={false} />
              <a onClick={handlePlayPause} onMouseEnter={handleMouseEnter}>
                <i
                  className={
                    isAudioPlaying ? "fa fa-volume-up" : "fa fa-volume-off"
                  }
                  aria-hidden="true"
                ></i>
              </a>
              <ul>
                <li>
                  <a
                    onMouseEnter={handleMouseEnter}
                    onClick={handleMouseClick}
                  >
                    <i className="fa fa-twitter" aria-hidden="true"></i>
                  </a>
                </li>
                <li>
                  <a
                    onMouseEnter={handleMouseEnter}
                    onClick={handleMouseClick}
                  >
                    <i className="fa fa-instagram" aria-hidden="true"></i>
                  </a>
                </li>
                <li>
                  <a
                    onMouseEnter={handleMouseEnter}
                    onClick={handleMouseClick}
                  >
                    <i className="fa fa-facebook" aria-hidden="true"></i>
                  </a>
                </li>
              </ul>
            </div>
          </div>
        </div>
        <img src={tringleicon} className="tringleicon" />
        <img src={dotspattern} className="dots-pattern" />
      </div>
    </div>
  );
}

export default Banner;
