import React, { useState } from "react";
import logo from "../../assets/images/logo.png";

function LoaderVideo(props) {
  const [isVideoEnded, setVideoEnded] = useState(false);
  const [isVideoStarted, setVideoStarted] = useState(false);
  const [loader, setLoader] = useState(true);
  const handleVideoEnded = () => setVideoEnded(true);
  const hideLoader = () => setLoader(false);
  window.addEventListener("load", (event) => {
    document.getElementById('loader-video').play();
    console.log("page is fully loaded");
  });
  return (
    <div>
      <div style={{ display: loader ? "flex" : "none" }} className="loader-sec">
        <img src={logo} alt="" />
        <p>Loading...</p>
      </div>
      <div
        className={isVideoStarted?'loader-video-sec active':'loader-video-sec'}
        style={{ display: isVideoEnded ? "none" : "flex" }}
      >
        <video
          className="loader-video"
          id="loader-video"
          src={props.src}
          muted={true}
          autoPlay={true}
          playsInline
          onEnded={handleVideoEnded}
          onLoadStart={hideLoader}
        ></video>
      </div>
    </div>
  );
}

export default LoaderVideo;
