import React, { useEffect, useMemo, useRef, useState } from "react";
import dotspattern from "../../assets/images/dots-pattern.png";
import tringleicon from '../../assets/images/pink-tringleicon.png'

function Gravitars({ setActiveLink, activeLink }) {
  const viewportRef = useRef(null);
  const isInViewport = useIsInViewport(viewportRef);

  useEffect(() => {
    if (isInViewport) {
      setActiveLink({
        ...activeLink,
        banner: false,
        intro: false,
        lore: false,
        features: false,
        gravitars: true,
        roadmap: false,
        cta: false,
      });
    } else {
      // setActiveLink({ ...activeLink, banner: false, intro: false, lore: false, features: false, gravitars: false, roadmap: false , cta: false });
    }
  }, [isInViewport]);

  function useIsInViewport(ref) {
    const [isIntersecting, setIsIntersecting] = useState(false);

    const observer = useMemo(
      () =>
        new IntersectionObserver(([entry]) =>
          setIsIntersecting(entry.isIntersecting)
        ),
      []
    );

    useEffect(() => {
      observer.observe(ref.current);

      return () => {
        observer.disconnect();
      };
    }, [ref, observer]);

    return isIntersecting;
  }

  return (
    <div id="gravitars" className="all-features-sec gravitors-sec">
      <div className="feature-sec feature-even-sec">
        <div className="container">
          <div className="feature-heading-sec">
            <h2 ref={viewportRef}>
              Your <span>Digital</span> self <span>Avaits</span> you in
              Gravitale
            </h2>
          </div>
          <div className="feature-desc">
            <h3>
              The Gravitar Species Is <span>Unique Blend</span> Of Organic And
              <br />
              <span>Artificial</span> Components, Possessing Both The
              <br />
              <span>Physical Abilities</span> And
              <br />
              <span>Consciousness</span> Of Living Beings, As Well As The
              <br />
              <span>Computational Power</span> And
              <br />
              <span>Durability</span> Of Machines.
            </h3>
          </div>
          <h1 className="placeholder-text">GRAVITARS</h1>
        </div>
        <img src={dotspattern} className="dots-pattern" />
        <img src={tringleicon} className="tringleicon" />
      </div>
    </div>
  );
}

export default Gravitars;
