import React, { useEffect, useMemo, useRef, useState } from "react";
import tringleicon from '../../assets/images/traingle-icon.png'
import pinktringleicon from '../../assets/images/pink-tringleicon.png'
import dotspattern from '../../assets/images/dots-pattern.png'

function Features({setActiveLink, activeLink}) {
  const viewportRef = useRef(null);
  const viewportRef1 = useRef(null);
  const isInViewport = useIsInViewport(viewportRef);
  const isInViewport1 = useIsInViewport(viewportRef1);

  useEffect(() => {
    if (isInViewport || isInViewport1) {
      setActiveLink({ ...activeLink, banner: false, intro: false, lore: false, features: true, gravitars: false, roadmap: false , cta: false });
    } else {
      // setActiveLink({ ...activeLink, banner: false, intro: false, lore: false, features: false, gravitars: false, roadmap: false , cta: false });
    }
  },[isInViewport, isInViewport1])

  function useIsInViewport(ref) {
    const [isIntersecting, setIsIntersecting] = useState(false);

    const observer = useMemo(
      () =>
        new IntersectionObserver(([entry]) =>
          setIsIntersecting(entry.isIntersecting)
        ),
      []
    );

    useEffect(() => {
      observer.observe(ref.current);

      return () => {
        observer.disconnect();
      };
    }, [ref, observer]);

    return isIntersecting;
  }

  return (
    <div id="features" className="all-features-sec">
      <div className="feature-sec first-feature">
        <div ref={viewportRef1} className="container">
          <div className="feature-heading-sec">
            <h2>
              <span>Collect</span>, Play, and <span>Trade</span>
            </h2>
          </div>
          <div className="feature-desc">
            <h3>
              Your island is packed with <br /> <span>mysterious fruits</span>
              <br />
              growing them to
              <br />
              <span>earn rewards</span>.
            </h3>
          </div>
          <h1 className="placeholder-text">FEATURES</h1>
        </div>
        <img src={tringleicon} className="tringleicon" />
        <img src={dotspattern} className="dots-pattern" />
      </div>
      <div className="feature-sec feature-even-sec second-feature">
        <div ref={viewportRef} className="container">
          <div className="feature-heading-sec">
            <h2>
              <span>Customize </span>your own <span>Land</span>
            </h2>
          </div>
          <div className="feature-desc">
            <h3>
              Use Your <span>Rewards</span> To Easily Transform <br/>
              Them Into A <span>Galactic Currency</span>.<br/>
              The Currency Can Be To <br/>
              Purchase <span>Land Upgrades</span>.
            </h3>
          </div>
          <h1 className="placeholder-text">FEATURES</h1>
        </div>
        <img src={dotspattern} className="dots-pattern" />
      </div>
      <div className="feature-sec third-feature">
        <div className="container">
          <div className="feature-heading-sec">
            <h2>
              Create your <span>Digital Identity</span>
            </h2>
          </div>
          <div className="feature-desc">
            <h3>
            Gravitars Are <span>Fancy</span>, Fun And<br/>
            <span>Enjoyable Character</span><br/>
            That Are Native To Gravitale.<br/>
            Use Them To <span>Create Your Avatar</span><br/> And Let Them <span>Repreaent</span> You.
            </h3>
          </div>
          <h1 className="placeholder-text">FEATURES</h1>
        </div>
        <img src={pinktringleicon} className="tringleicon" />
        <img src={dotspattern} className="dots-pattern" />
      </div>

    </div>
  );
}

export default Features;
